'use client';

import { useState, useEffect } from 'react';
import { Plus, Edit, Trash2, X, Save, UsersRound } from 'lucide-react';

type ParishGroup = {
  id: number;
  nombre: string;
  descripcion: string;
  imagen_url: string;
  categoria: string;
  horario: string;
  contacto: string;
  orden: number;
};

const CATEGORIAS = ['liturgia', 'formacion', 'social', 'juvenil', 'otro'];

const emptyGroup: Omit<ParishGroup, 'id'> = {
  nombre: '',
  descripcion: '',
  imagen_url: '',
  categoria: 'otro',
  horario: '',
  contacto: '',
  orden: 0,
};

export default function ParishGroupsPage() {
  const [groups, setGroups] = useState<ParishGroup[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingGroup, setEditingGroup] = useState<ParishGroup | null>(null);
  const [formData, setFormData] = useState(emptyGroup);
  const [isSaving, setIsSaving] = useState(false);
  const [categoryFilter, setCategoryFilter] = useState('all');

  useEffect(() => {
    fetchGroups();
  }, []);

  const fetchGroups = async () => {
    try {
      const response = await fetch('/api/admin/parish-groups');
      const data = await response.json();
      if (data.success) {
        setGroups(data.data);
      }
    } catch (error) {
      console.error('Error al cargar grupos:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const openCreateModal = () => {
    setEditingGroup(null);
    setFormData({ ...emptyGroup, orden: groups.length + 1 });
    setIsModalOpen(true);
  };

  const openEditModal = (group: ParishGroup) => {
    setEditingGroup(group);
    setFormData({
      nombre: group.nombre,
      descripcion: group.descripcion,
      imagen_url: group.imagen_url,
      categoria: group.categoria,
      horario: group.horario,
      contacto: group.contacto,
      orden: group.orden,
    });
    setIsModalOpen(true);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSaving(true);

    try {
      const url = editingGroup ? `/api/admin/parish-groups/${editingGroup.id}` : '/api/admin/parish-groups';
      const response = await fetch(url, {
        method: editingGroup ? 'PUT' : 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(formData),
      });

      if (response.ok) {
        fetchGroups();
        setIsModalOpen(false);
      } else {
        const error = await response.json();
        alert(error.message || 'Error al guardar');
      }
    } catch (error) {
      console.error('Error:', error);
      alert('Error al guardar');
    } finally {
      setIsSaving(false);
    }
  };

  const deleteGroup = async (id: number) => {
    if (!confirm('¬øEst√°s seguro de eliminar este grupo?')) return;

    try {
      const response = await fetch(`/api/admin/parish-groups/${id}`, { method: 'DELETE' });
      if (response.ok) {
        setGroups(groups.filter(g => g.id !== id));
      }
    } catch (error) {
      console.error('Error:', error);
    }
  };

  const filteredGroups = categoryFilter === 'all' 
    ? groups 
    : groups.filter(g => g.categoria === categoryFilter);

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[60vh]">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Cargando grupos...</p>
        </div>
      </div>
    );
  }

  return (
    <div>
      <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
        <div className="flex items-center justify-between flex-wrap gap-4">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Grupos Parroquiales</h1>
            <p className="text-gray-600 mt-1">Gestiona los grupos y movimientos parroquiales</p>
          </div>
          <button onClick={openCreateModal} className="flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors">
            <Plus className="w-5 h-5" />
            Agregar Grupo
          </button>
        </div>
      </div>

      <div className="bg-white rounded-lg shadow-sm p-4 mb-6">
        <div className="flex items-center gap-2 flex-wrap">
          <span className="text-sm font-medium text-gray-700">Filtrar por categor√≠a:</span>
          <select
            value={categoryFilter}
            onChange={(e) => setCategoryFilter(e.target.value)}
            className="px-3 py-1.5 border border-gray-300 rounded-lg text-sm focus:ring-2 focus:ring-blue-500"
          >
            <option value="all">Todas</option>
            {CATEGORIAS.map(cat => (
              <option key={cat} value={cat}>{cat.charAt(0).toUpperCase() + cat.slice(1)}</option>
            ))}
          </select>
          <span className="text-sm text-gray-500 ml-auto">{filteredGroups.length} grupos</span>
        </div>
      </div>

      {filteredGroups.length > 0 ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredGroups.map((group) => (
            <div key={group.id} className="bg-white rounded-lg shadow-sm overflow-hidden">
              <div className="relative h-40 bg-gray-100">
                {group.imagen_url ? (
                  <img src={group.imagen_url} alt={group.nombre} className="w-full h-full object-cover" />
                ) : (
                  <div className="w-full h-full flex items-center justify-center">
                    <UsersRound className="w-16 h-16 text-gray-400" />
                  </div>
                )}
                <span className="absolute top-2 left-2 px-2 py-1 text-xs font-medium rounded bg-blue-600 text-white capitalize">
                  {group.categoria}
                </span>
              </div>
              
              <div className="p-4">
                <h3 className="font-semibold text-gray-900 text-lg">{group.nombre}</h3>
                {group.descripcion && <p className="text-sm text-gray-600 mt-1 line-clamp-2">{group.descripcion}</p>}
                {group.horario && <p className="text-xs text-gray-500 mt-2">üïê {group.horario}</p>}
                {group.contacto && <p className="text-xs text-gray-500">üìû {group.contacto}</p>}
              </div>
              
              <div className="px-4 pb-4 flex items-center justify-end gap-2">
                <button onClick={() => openEditModal(group)} className="p-2 text-blue-600 hover:bg-blue-50 rounded-lg transition-colors" title="Editar">
                  <Edit className="w-5 h-5" />
                </button>
                <button onClick={() => deleteGroup(group.id)} className="p-2 text-red-600 hover:bg-red-50 rounded-lg transition-colors" title="Eliminar">
                  <Trash2 className="w-5 h-5" />
                </button>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <div className="bg-white rounded-lg shadow-sm p-12 text-center">
          <UsersRound className="w-16 h-16 text-gray-400 mx-auto mb-4" />
          <h3 className="text-lg font-semibold text-gray-900 mb-2">No hay grupos</h3>
          <p className="text-gray-600 mb-4">Agrega el primer grupo parroquial</p>
          <button onClick={openCreateModal} className="inline-flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700">
            <Plus className="w-5 h-5" />
            Agregar Grupo
          </button>
        </div>
      )}

      {isModalOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg max-w-2xl w-full max-h-[90vh] overflow-hidden">
            <div className="p-6 border-b border-gray-200">
              <div className="flex items-center justify-between">
                <h2 className="text-xl font-bold text-gray-900">{editingGroup ? 'Editar Grupo' : 'Agregar Grupo'}</h2>
                <button onClick={() => setIsModalOpen(false)} className="p-2 text-gray-400 hover:text-gray-600 transition-colors">
                  <X className="w-6 h-6" />
                </button>
              </div>
            </div>

            <form onSubmit={handleSubmit}>
              <div className="p-6 space-y-4 overflow-y-auto max-h-[60vh]">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Nombre *</label>
                  <input type="text" value={formData.nombre} onChange={(e) => setFormData({ ...formData, nombre: e.target.value })} required className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent" placeholder="Nombre del grupo" />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Categor√≠a *</label>
                  <select value={formData.categoria} onChange={(e) => setFormData({ ...formData, categoria: e.target.value })} className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent">
                    {CATEGORIAS.map(cat => (
                      <option key={cat} value={cat}>{cat.charAt(0).toUpperCase() + cat.slice(1)}</option>
                    ))}
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Descripci√≥n</label>
                  <textarea value={formData.descripcion} onChange={(e) => setFormData({ ...formData, descripcion: e.target.value })} rows={3} className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent" placeholder="Descripci√≥n del grupo" />
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Horario</label>
                    <input type="text" value={formData.horario} onChange={(e) => setFormData({ ...formData, horario: e.target.value })} className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent" placeholder="Ej: S√°bados 4pm" />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">Contacto</label>
                    <input type="text" value={formData.contacto} onChange={(e) => setFormData({ ...formData, contacto: e.target.value })} className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent" placeholder="Tel√©fono o email" />
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">URL de Imagen</label>
                  <input type="text" value={formData.imagen_url} onChange={(e) => setFormData({ ...formData, imagen_url: e.target.value })} className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent" placeholder="/images/grupos/grupo.jpg" />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Orden</label>
                  <input type="number" value={formData.orden} onChange={(e) => setFormData({ ...formData, orden: parseInt(e.target.value) })} min="1" className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent" />
                </div>
              </div>

              <div className="p-6 border-t border-gray-200 bg-gray-50 flex justify-end gap-3">
                <button type="button" onClick={() => setIsModalOpen(false)} className="px-4 py-2 text-gray-700 bg-white border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors">Cancelar</button>
                <button type="submit" disabled={isSaving} className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50 flex items-center gap-2">
                  {isSaving ? (
                    <>
                      <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                      Guardando...
                    </>
                  ) : (
                    <>
                      <Save className="w-4 h-4" />
                      {editingGroup ? 'Actualizar' : 'Agregar'}
                    </>
                  )}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
